@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");

:root {
  /* Base font size */
  font-size: 10px;

  /* Set neon color */
  --neon-text-color: #f40;
  --neon-border-color: #08f;
  --color2: white;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 100vh;
  background: #010615;
  flex-wrap: wrap;
}

.box {
  position: relative;
  width: 15vw;
  height: 35vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px;
  transition: 0.5s;
  background: #060c21;
}

.box .imgBx {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  overflow: hidden;
}

.box .imgBx img {
  max-width: 100%;
  opacity: 0.1;
  transition: 0.5s;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.box:hover .imgBx img {
  opacity: 1;
  transform: scale(1.1);
}

.box::before {
  content: " ";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: #fff;
  z-index: -1;
}
.box::after {
  content: " ";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: #fff;
  z-index: -2;
  filter: blur(40px);
}

.box:nth-child(4)::before,
.box:nth-child(4)::after {
  background: linear-gradient(235deg, #00b9a1, #4835b8,#4255ff );
}

.box:nth-child(3)::before,
.box:nth-child(3)::after {
  background: linear-gradient(235deg, #002ab4, #008fbb, #ac8852);
}
.box:nth-child(2)::before,
.box:nth-child(2)::after {
  background: linear-gradient(235deg, #bb557b, #7d378b, #2d9abb);
}
.box::before,
.box::after {
  background: linear-gradient(235deg, #ec8f42, #0a5ebe, #d3772c);
}
