.box:hover {
  height: 45vh;
  width: 20vw;
}

.box .content {
  position: absolute;
  bottom: 0;
  left: 10px;
  right: 10px;
  bottom: 10px;
  height: 30%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  opacity: 0;
  transition: 0.5s;
}

.box:hover .content {
  opacity: 1;
}

.box .content h2 {
  font-size: 20px;
  color: #fff;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 1px;
}

.box .content h2 span {
  font-size: 14px;
  color: #fff;
  font-weight: 200;
  line-height: 30px;
  letter-spacing: 2px;
}

.neon {
  animation: flicker 1.5s infinite alternate;
}

.member {
  position: absolute;
  /* height: 20vh; */
  top: 2vh;

  margin-top: 0;
  font-size: 5rem;
  font-weight: 1;
  font-style: italic;
  color: #fff;
  padding: 4rem 10rem;
  border: 0.4rem solid #fff;
  border-radius: 2rem;
  text-align: center;
  align-content: center;
}
.member a {
  color:#fff;
  text-decoration: none;
}
.footer a {
  color:#fff;
  text-decoration: none;
}
.neon::-moz-selection {
  background-color: var(--neon-border-color);
  color: var(--neon-text-color);
}

.neon::selection {
  background-color: var(--neon-border-color);
  color: var(--neon-text-color);
}

.neon:focus {
  outline: none;
}
@keyframes flicker {
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    text-shadow: -0.2rem -0.2rem 1rem #fff, 0.2rem 0.2rem 1rem #fff,
      0 0 2rem var(--neon-text-color), 0 0 4rem var(--neon-text-color),
      0 0 6rem var(--neon-text-color), 0 0 8rem var(--neon-text-color),
      0 0 10rem var(--neon-text-color);

    box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff,
      0 0 2rem var(--neon-border-color), inset 0 0 2rem var(--neon-border-color),
      0 0 4rem var(--neon-border-color), inset 0 0 4rem var(--neon-border-color);
  }

  20%,
  24%,
  55% {
    text-shadow: none;
    box-shadow: none;
  }
}

@keyframes flickerfooter {
  0%,
  19%,
  29%,
  54%,
  76%,
  100% {
    text-shadow: -0.2rem -0.2rem 1rem #fff, 0.2rem 0.2rem 1rem #fff,
      0 0 1rem var(--neon-text-color), 0 0 0.2rem var(--neon-text-color),
      0 0 0.2rem var(--neon-text-color), 0 0 0.4rem var(--neon-text-color),
      0 0 1rem var(--neon-text-color);

    /* box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff,
