      0 0 4rem var(--neon-border-color), inset 0 0 4rem var(--neon-border-color); */
  }

  20%,
  24%,
  55% {
    text-shadow: none;
    box-shadow: none;
  }
}

.footer {
  position: absolute;
  bottom: 5vh;
  right: 5vh;
  font-size: 3rem;
  color: rgb(255, 255, 255);
  text-align: right;
}

.neonfooter:hover {
